import * as React from 'react';
import PropTypes from 'prop-types';
import { Link, graphql } from 'gatsby';
import Layout from '../components/layout';
import * as style from '../styles/style.module.css';

const TagPage = ({ pageContext, data }) => {
  const { tag } = pageContext;
  const { edges, totalCount } = data.allMdx;
  const tagHeader = `タグ「${tag}」が付いた記事（${totalCount}件）`;
  return (
    <Layout
      pageType="blog"
      pageTitle={tagHeader}
      pageDescription={`タグ「${tag}」が付いた記事の一覧ページです。${totalCount}件あります。`}
    >
      <ul className={style.blogList}>
        {edges.map(({ node }) => {
          const slug = node.slug;
          const { title } = node.frontmatter;
          return (
            <li key={slug} className={style.blogList__item}>
              <Link to={`/memo/${slug}`} className={style.blogList__link}>
                {title}
              </Link>
            </li>
          )
        })}
      </ul>
      <div className={style.pageBack}>
        <Link to="/tag-list">タグリストに戻る</Link>
      </div>
    </Layout>
  )
};

TagPage.propTypes = {
  pageContext: PropTypes.shape({
    tag: PropTypes.string.isRequired,
  }),
  data: PropTypes.shape({
    allMdx: PropTypes.shape({
      totalCount: PropTypes.number.isRequired,
      edges: PropTypes.arrayOf(
        PropTypes.shape({
          node: PropTypes.shape({
            frontmatter: PropTypes.shape({
              title: PropTypes.string.isRequired,
            }),
            slug: PropTypes.string.isRequired,
          }),
        }).isRequired
      ),
    }),
  }),
};

export default TagPage;

export const pageQuery = graphql`
  query($tag: String) {
    allMdx(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      edges {
        node {
          slug
          frontmatter {
            title
          }
        }
      }
    }
  }
`;
